// vmx is where I store variables and mixins
// Always clean your code before you go to bed.

// ----------------------------------------------------- //
@import 'vmx';
// New Material Colors - transfer to vmx after you confirm
$text-color: #EAEAEA;
$project-bg: #112746;
$project-link: #F29292;
$dark-solid-bg: #0D212D;
// replace with a blend mode

$intro-link: #49BD9D;
$headline: #faa82e;
$resume-bg: #252742;
$contact-bg: #243041;
$contact-link: #49ace1;
$nav-text: #C0B3A0;
$button-primary: #d24376;
$button-secondary: #CCCCCC;
$gradient-top: #eea849;
$gradient-bottom: #f46b45;
$light-purple: #A1BAE3;

$black: #000000;
$white: #FFFFFF;
$button-shadow: rgba($black, 0.25);
$wrapper-shadow: rgba($black, 0.25);
$button-radius: $atom-spacing*0.5;
// Font sizes
$fs-default: 18px;
$fs-section-head: 3em;
$fs-button: 1.2em;
$fs-intro: 1.5em;

// Grid Guides - turn off
$visual-grid: false;
$visual-grid-color: green;
$visual-grid-index: front;
$visual-grid-opacity: 0.05;

// Bourbon and Neat Imports
@import 'bourbon';
@import 'neat';

// TODO: do a final verification
$elements: front, intro-wrapper, profile, intro, contact-wrap, contact, resume-wrap, resume, projects-wrap, projects, project, nav-wrap, nav, logo, slide-menu, slide-menu-logo, hamburger;

// Headline font
@font-face {
	font-family: 'headline';
	src: url('../fonts/Aleo-Bold.otf') format('opentype');
	font-weight: normal;
	font-style: normal; }
// For Wrapper Divs
@mixin padding-wrap {
	@include bp-papa-max {
		padding: $atom-spacing*2; } }
// Buttons
@mixin button-general {
	white-space: nowrap;
	display: inline-block;
	height: $atom-spacing*6;
	line-height: $atom-spacing*6;
	padding: 0 $atom-spacing*4;
	@include box-shadow(0, $atom-spacing*0.5, $atom-spacing, $button-shadow);
	border-radius: $button-radius;
	font-size: 0.9em;
	text-transform: uppercase;
	text-decoration: none;
	text-align: center;
	cursor: pointer;
	@include transition(ease, 0.3s);
	&:hover {
		@include transform(translate(0px, -$atom-spacing*0.25)); } }
.primary-button {
	@include button-general;
	background-color: $button-primary;
	color: $text-color; }
.secondary-button {
	@include button-general;
	background-color: $button-secondary;
	color: $dark-solid-bg; }
.button-fa {
	margin: 0 $atom-spacing*0.5 0 0; }
// TODO: Button hover states

@mixin move-in {
	opacity: 0;
	position: relative;
	@include transform(translate(0px, $atom-spacing*5));
	@include transition(opacity 1s ease-in-out, transform 0.5s ease-in-out);
	&.appear {
		opacity: 1;
		@include transform(translate(0px, 0));
		@include transition(opacity 1s ease-in-out, transform 0.5s ease-in-out); } }

@mixin appear-soft {
	opacity: 0;
	@include transition(opacity 0.5s ease-in-out);
	&.appear {
		opacity: 1;
		@include transition(opacity 0.5s ease-in-out); } }

// Global
h1, h2, h3 {
	margin: 0; }
p {
	margin-top: 0; }
a {
	text-decoration: none; }
body {
	background-color: $dark-solid-bg;
	background-image: url(../bg.png);
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
	background-attachment: fixed;
	margin: 0;
	padding: 0;
	font-size: $fs-default;
	font-family: $primary-font, $secondary-font, sans-serif;
	color: $text-color; }
.baby-hide-text {
	display: none;
	@include bp-parent-bears {
		display: inline; } }
.baby-show-text {
	display: inline;
	@include bp-parent-bears {
		display: none; } }
.baby-hide-div {
	display: none;
	@include bp-parent-bears {
		display: block; } }
.baby-show-div {
	display: block;
	margin-top: $atom-spacing*5;
	@include bp-parent-bears {
		display: none; } }
.front {
	height: 100vh;
	background-position: center;
	z-index: index($elements, front);
	position: relative;
	background-attachment: fixed; }
.key-term {
	color: $contact-link;
	font-style: italic; }
.intro-wrapper {
	@include outer-container;
	@include clearfix;
	display: flex;
	flex-direction: column;
	justify-content: center;
	height: 80vh;
	@include padding-wrap;
	z-index: index($elements, intro-wrapper);
	.intro {
		z-index: index($elements, intro);
		@include bp-baby-bear {
			@include span-columns(12); }
		@include bp-mama-bear {
			@include span-columns(10); }
		@include bp-parent-bears {
			@include span-columns(6);
			font-size: 1.05em; }
		.headline {
			margin-bottom: $atom-spacing*4;
			h1 {
				@include bp-papa-max {
					font-size: 2.7em; }
				@include bp-beyond-papa {
					font-size: 3.5em; }
				font-family: 'headline';
				font-weight: normal;
				text-transform: lowercase;
				line-height: 1.1em;
				color: $headline; }
			.de-emph-headline {
				-webkit-text-fill-color: rgba(lighten($headline, 10%), 0.15); } }
		.typed-wrap {
			height: $atom-spacing*7;
			a {
				color: $intro-link;
				border-bottom: 1px dotted $intro-link; } }
		a.intro-button {
			border: none;
			@include bp-papa-max {
				margin: $atom-spacing*3 $atom-spacing 0 0;
				padding: 0 $atom-spacing*2; }
			@include bp-beyond-papa {
				margin: $atom-spacing*4 $atom-spacing*2 $atom-spacing*4 0; } } } }
.typed-cursor {
	margin-left: $atom-spacing*0.5;
	opacity: 1;
	color: $headline;
	animation: blink 0.7s infinite;
	@include keyframes(blink) {
		0% {
			opacity: 1; }
		50% {
			opacity: 0; }
		100% {
			opacity: 1; } } }

.profile {
	background-image: url(../img/profile2.png);
	background-repeat: no-repeat;
	background-position: center right;
	background-size: contain;
	opacity: 0.2;
	position: absolute;
	bottom: -$atom-spacing*15;
	right: auto;
	display: block;
	padding: 0;
	width: 100%;
	height: 100%;
	z-index: index($elements, profile);
	@include bp-baby-bear {
		background: none; } }
.hamburger {
	cursor: pointer;
	position: fixed;
	color: $dark-solid-bg;
	font-size: 0.75em;
	height: $atom-spacing*6;
	width: $atom-spacing*6;
	text-align: center;
	padding-top: $atom-spacing*1.25;
	right: $atom-spacing*2;
	top: $atom-spacing*2;
	z-index: index($elements, hamburger);
	background: rgba($text-color, 0.8);
	border: 1px solid rgba($dark-solid-bg, 0.2);
	border-radius: 50%;
	@include bp-parent-bears {
		display: none; } }
.slide-menu {
	@include bp-parent-bears {
		display: none; }
	@include position(fixed, -100vh 0 0 0px);
	opacity: 0;
	@include transition(opacity 0.6s ease-in-out 0.0s, top 0.4s ease-in-out 0.2s, display 0.0s);
	height: 100vh;
	z-index: index($elements, slide-menu);
	background-color: rgba($dark-solid-bg, 0.98);
	text-align: center;
	.ham-logo {
		cursor: pointer;
		z-index: index($elements, slide-menu-logo);
		margin: $atom-spacing*15 auto $atom-spacing*6 auto;
		img {
			height: $atom-spacing*10; } }
	nav {
		color: $nav-text; }
	ul {
		list-style: none;
		margin: 0;
		padding: 0;
		text-transform: uppercase;
		li {
			cursor: pointer;
			padding: $atom-spacing*4 0;
			width: 95vw;
			margin: 0 auto;
			border-bottom: 1px dotted $text-color; }
		a {
			color: inherit; } } }
#slide-menu-toggler {
	display: none;
	&:checked ~ .slide-menu {
		top: 0px;
		opacity: 0.98;
		display: block; } }
.nav-wrap {
	margin: 0;
	width: 100%;
	box-sizing: border-box;
	// background: $project-bg
	color: $nav-text;
	height: $atom-spacing*10;
	position: absolute;
	bottom: 0;
	z-index: index($elements, nav-wrap);
	.nav {
		display: none;
		@include bp-parent-bears {
			display: block; }
		max-width: $max-width;
		@include outer-container;
		margin: 0 auto;
		padding: $atom-spacing*2 0;
		.logo {
			opacity: 1 !important;
			@include span-columns(2);
			background-image: url(../img/logo.png);
			background-repeat: no-repeat;
			background-size: contain;
			height: $atom-spacing*9;
			width: 30%;
			z-index: index($elements, logo);
			@include bp-baby-bear {
				margin-left: $atom-spacing; }
			@include bp-mama-bear {
				margin-left: $atom-spacing*2; }
			@include bp-papa-bear {
				margin-left: $atom-spacing*2; }
			@include bp-monster-bear {
				margin-left: $atom-spacing*2; } }
		nav {
			display: flex;
			justify-content: flex-end;
			ul {
				box-sizing: border-box;
				list-style: none;
				margin: 0;
				padding: 0;
				li {
					float: left;
					text-transform: uppercase;
					margin-right: $atom-spacing*5;
					line-height: $atom-spacing*6;
					a {
						color: inherit; } } } } } }
// TODO: use the border mixin for browser compatibility
.fixed {
	position: fixed;
	background: $project-bg;
	top: 0;
	border-bottom: 1px dashed rgba($nav-text, 0.5);
	display: none;
	@include bp-parent-bears {
		display: block; } }
.section-header {
	margin: $atom-spacing*8 0;
	text-align: left;
	text-transform: uppercase;
	font-size: 1.8em;
	font-weight: bold; }
.projects-wrap {
	margin-top: -$atom-spacing*15;
	@include bp-baby-bear {
		margin-top: -$atom-spacing*8; }
	@include bp-mama-bear {
		margin-top: -$atom-spacing*8; }
	// only for projects-wrap
	@include bp-baby-bear {
		padding: $atom-spacing*10 $atom-spacing*2 $atom-spacing*4 $atom-spacing*2; }
	@include bp-mama-bear {
		padding: $atom-spacing*10 $atom-spacing*2 $atom-spacing*4 $atom-spacing*2; }
	padding: $atom-spacing*24 $atom-spacing*2 $atom-spacing*4 $atom-spacing*2;
	background: $project-bg;
	position: relative;
	@include transform(skew(0, -2.5deg));
	z-index: index($elements, projects-wrap);
	@include box-shadow(0, 0, $atom-spacing, $black);
	.projects {
		@include outer-container;
		@include transform(skew(0, 2.5deg));
		@include clearfix;
		position: relative;
		z-index: index($elements, projects);
		ul {
			list-style: none;
			margin: 0;
			padding: 0;
			li {
				margin: $atom-spacing*8 0 $atom-spacing*12 0;
				.project {
					@include bp-parent-bears {
						@include move-in; }
					z-index: index($elements, project);
					@include clearfix;
					@include bp-papa-max {
						display: block; }
					@include bp-parent-bears {
						display: flex;
						flex-direction: row; }
					.project-img {
						display: block;
						margin: 0;
						padding: 0;
						vertical-align: top;
						@include bp-baby-bear {
							@include span-columns(12); }
						@include bp-mama-bear {
							@include span-columns(12); }
						@include bp-parent-bears {
							@include span-columns(7); }
						img {
							max-width: 100%;
							height: auto;
							display: block; } }
					.project-text {
						box-sizing: border-box;
						@include bp-papa-max {
							@include span-columns(12);
							margin-top: $atom-spacing*3; }
						@include bp-parent-bears {
							@include span-columns(5);
							padding: 0 $atom-spacing*2; }
						h2 {
							margin: 0;
							padding: 0; }
						a.project-button {
							cursor: pointer;
							border: none;
							color: $project-bg;
							// TODO: button margins need to be defined better
							@include bp-papa-max {
								margin: $atom-spacing 0 $atom-spacing 0;
								padding: 0 $atom-spacing*2; }
							@include bp-beyond-papa {
								margin: $atom-spacing $atom-spacing*2 $atom-spacing*3 0; } } } } } } } }
.resume-wrap {
	@include bp-baby-bear {
		margin-top: 0; }
	@include bp-mama-bear {
		margin-top: 0; }
	margin-top: $atom-spacing*5;
	padding: $atom-spacing*8 $atom-spacing*2 $atom-spacing*4 $atom-spacing*2;
	background: $resume-bg;
	position: relative;
	@include transform(skew(0, 2.5deg));
	z-index: index($elements, resume-wrap);
	@include box-shadow(0, 0, $atom-spacing, $black);
	.resume {
		@include transform(skew(0, -2.5deg));
		@include clearfix;
		@include outer-container;
		position: relative;
		z-index: index($elements, resume); } }
.bio {
	@include clearfix;
	.bio-left {
		padding: 0;
		@include bp-papa-max {
			@include span-columns(12);
			margin-bottom: $atom-spacing*6; }
		@include bp-parent-bears {
			@include span-columns(6); } }
	.bio-right {
		background: $white;
		padding: $atom-spacing*2;
		border-radius: $button-radius;
		color: $dark-solid-bg;
		@include bp-papa-max {
			@include span-columns(12);
			margin-bottom: $atom-spacing*6; }
		@include bp-parent-bears {
			@include span-columns(6); }
		p {
			font-size: 0.9em; }
		.bio-key {
			color: $intro-link;
			text-transform: uppercase;
			font-weight: bold; }
		a.resume-button {
			border: none;
			@include bp-papa-max {
				margin: $atom-spacing*2 $atom-spacing 0 0;
				padding: 0 $atom-spacing*2; }
			@include bp-beyond-papa {
				margin: $atom-spacing*2 $atom-spacing*2 $atom-spacing*2 0; }
			@include box-shadow(0, 0, $atom-spacing*0.5, $button-shadow); } } }
.bio-key {
	color: $headline;
	font-weight: bold;
	text-transform: uppercase; }
.skills {
	display: block;
	margin: $atom-spacing*6 auto;
	@include clearfix;
	.skill {
		@include bp-parent-bears {
			@include move-in; }
		@include bp-baby-bear {
			@include span-columns(12);
			margin-bottom: $atom-spacing*6; }
		@include bp-mama-bear {
			@include span-columns(12);
			margin-bottom: $atom-spacing*6; }
		@include bp-parent-bears {
			@include span-columns(4); }
		.skill-img {
			margin: $atom-spacing*3 0;
			align-items: center;
			img {
				margin: 0 auto;
				width: 70%;
				height: auto;
				display: block; } }
		.skill-text {
			h2 {
				text-transform: uppercase;
				font-size: 1em; }
			padding: $atom-spacing*2 0; } } }
.contact-wrap {
	@include bp-baby-bear {
		margin-top: 0; }
	@include bp-mama-bear {
		margin-top: 0; }
	margin-top: $atom-spacing*5;
	padding: $atom-spacing*8 $atom-spacing*2 $atom-spacing*12 $atom-spacing*2;
	background: $contact-bg;
	position: relative;
	@include transform(skew(0, -2.5deg));
	z-index: index($elements, contact-wrap);
	@include box-shadow(0, 0, $atom-spacing, $black);
	.contact {
		@include transform(skew(0, 2.5deg));
		@include clearfix;
		@include outer-container;
		position: relative;
		z-index: index($elements, contact);
		.contact-left {
			@include bp-baby-bear {
				@include span-columns(12);
				margin-bottom: $atom-spacing*6; }
			@include bp-mama-bear {
				@include span-columns(12);
				margin-bottom: $atom-spacing*6; }
			@include bp-parent-bears {
				@include span-columns(6); }
			.headshot {
				@include bp-baby-bear {
					margin-bottom: $atom-spacing*2; }
				@include bp-mama-bear {
					margin-bottom: $atom-spacing*2; }
				@include bp-parent-bears {
					margin-bottom: $atom-spacing*4; }
				border-radius: $button-radius;
				overflow: hidden;
				position: relative;
				img {
					@include bp-baby-bear {
						width: 100%;
						height: auto;
						display: block;
						// position: absolute
						// top: -10%
 }						// left: -40%
					@include bp-mama-bear {
						width: 100%;
						height: auto;
						display: block;
						// position: absolute
						// top: -12%
 }						// left: -10%
					width: 100%;
					height: auto;
					display: block;
					// position: absolute
 } }					// top: -$atom-spacing*5
			.contact-note {
				margin-top: 0; } }
		.contact-form {
			@include bp-papa-max {
				@include span-columns(12);
				margin-bottom: $atom-spacing*6; }
			@include bp-parent-bears {
				@include span-columns(6); }
			label {
				font-size: 1em;
				color: $contact-link;
				margin-bottom: $atom-spacing;
				@include span-columns(12); }
			input[type="email"], input[type="text"], textarea {
				font-family: inherit;
				background-color: lighten($text-color, 20%);
				border-radius: $button-radius;
				color: $dark-solid-bg;
				padding: $atom-spacing;
				margin-bottom: $atom-spacing*4;
				width: 100%;
				font-size: 0.9em; }
			input[type="submit"] {
				border: none;
				font-family: inherit;
				line-height: $atom-spacing*6;
				padding: 0 $atom-spacing*4;
				@include box-shadow(0, $atom-spacing*0.5, $atom-spacing, $button-shadow);
				border-radius: $button-radius;
				font-size: 0.9em;
				text-transform: uppercase;
				text-align: center;
				background-color: $button-primary;
				color: $text-color; } } } }
.footer-wrap {
	margin-top: $atom-spacing*5;
	padding: $atom-spacing*8 $atom-spacing*2 $atom-spacing*4 $atom-spacing*2;
	position: relative;
	.footer {
		font-size: 0.8em;
		@include outer-container;
		@include clearfix;
		h2.footer {
			text-transform: uppercase;
			font-weight: bold;
			font-size: 1em;
			margin-bottom: $atom-spacing; }
		.credits {
			opacity: 0.8;
			@include bp-papa-max {
				@include span-columns(10);
				@include shift(1);
				margin-bottom: $atom-spacing*6; }
			@include bp-beyond-papa {
				@include span-columns(4); }
			a {
				color: $intro-link;
				border-bottom: 1px dotted $intro-link; } }
		.map-wrap {
			@include bp-papa-max {
				@include span-columns(10);
				@include shift(1);
				margin-bottom: $atom-spacing*6; }
			@include bp-beyond-papa {
				@include span-columns(4);
				margin-bottom: $atom-spacing*4; }
			h2.footer {
				opacity: 0.8; }
			.map {
				border-radius: $button-radius;
				overflow: hidden;
				height: $atom-spacing*32;
				iframe {
					width: 100%;
					height: 100%; } } }
		.social {
			margin: 0 auto;
			a {
				color: $intro-link;
				text-decoration: none; }
			opacity: 0.6;
			@include bp-papa-max {
				@include span-columns(10);
				@include shift(1);
				text-align: center; }
			@include bp-beyond-papa {
				text-align: center;
				@include span-columns(4);
				margin-top: $atom-spacing*4; }
			.footer-icon {
				@include bp-baby-bear {
					font-size: 1.5em; }
				@include bp-mama-bear {
					font-size: 1.8em; }
				@include bp-parent-bears {
					font-size: 2em; }
				font-weight: normal;
				margin-right: $atom-spacing*3;
				margin-bottom: $atom-spacing*3; } } }
	.footer-text {
		margin: $atom-spacing*10 auto $atom-spacing*4 auto;
		font-size: 0.8em;
		opacity: 0.6;
		text-align: center; } }
// Resume time-line
#timeline {
	display: block; }
#cd-timeline {
	position: relative;
	&::before {
		/* this is the vertical line */
		content: '';
		position: absolute;
		top: 0;
		left: 18px;
		height: 100%;
		width: $atom-spacing*0.25;
		background: #faa82e;
 }		// rgba($text-color, 0.1)
	@include bp-parent-bears {
		margin: {
			top: $atom-spacing*6;
			bottom: $atom-spacing*6; }
		&::before {
			left: 50%;
			margin-left: -2px; } } }
.cd-timeline-block {
	position: relative;
	margin: 2em 0;
	@include clearfix;
	&:first-child {
		margin-top: 0; }
	&:last-child {
		margin-bottom: 0; }

	@include bp-parent-bears {
		margin: 4em 0;
		&:first-child {
			margin-top: 0; }
		&:last-child {
			margin-bottom: 0; } } }
.present {
	color: $button-primary;
	font-style: italic; }
.cd-timeline-img {
	position: absolute;
	top: 0;
	left: 0;
	font-size: 0.9em;
	width: $atom-spacing*5;
	height: $atom-spacing*5;
	line-height: $atom-spacing*5;
	border-radius: 50%;
	background-color: $resume-bg;
	color: $light-purple;
	text-align: center;
	padding: $atom-spacing 0;
	@include bp-parent-bears {
		padding: $atom-spacing*1.5 0;
		font-size: 1em;
		width: 60px;
		height: 60px;
		left: 50%;
		margin-left: -30px;

		/* Force Hardware Acceleration in WebKit */
		-webkit-transform: translateZ(0);
		-webkit-backface-visibility: hidden;

		.cssanimations &.is-hidden {
			visibility: hidden; }

		.cssanimations &.bounce-in {
			visibility: visible;
			@include animation(cd-bounce-1 .6s); } } }
@include keyframes(cd-bounce-1) {
	0% {
		opacity: 0;
		@include transform(scale(.5)); }

	60% {
		opacity: 1;
		@include transform(scale(1.2)); }

	100% {
		@include transform(scale(1)); } }
.cd-timeline-content {
	position: relative;
	margin-left: 60px;
	background: $white;
	@include border-radius;
	padding: $atom-spacing*2;
	box-shadow: 0 0 $atom-spacing*0.5 darken($white, 15%);
	@include clearfix;
	img {
		display: block;
		box-sizing: border-box;
		max-height: $atom-spacing*6;
		margin-bottom: $atom-spacing*1; }
	h2 {
		font-size: 1em;
		text-transform: uppercase;
		color: $dark-solid-bg; }
	.cd-read-more, .cd-date {
		display: inline-block; }
	h3 {
		font-size: 1em;
		color: lighten($dark-solid-bg, 10%); }
	p {
		color: lighten($dark-solid-bg, 10%);
		margin-bottom: 8px;
		margin-top: 8px; }
	a {
		color: darken($intro-link, 15%);
		border-bottom: 1px dotted darken($intro-link, 15%); }

	.cd-date {
		color: darken($headline, 25%);
		@include bp-parent-bears {
			color: $headline; }
		float: left;
		padding: .8em 0; }

	&::before {
		content: '';
		position: absolute;
		top: 16px;
		right: 100%;
		height: 0;
		width: 0;
		border: 7px solid transparent;
		border-right: 7px solid $white; }

	@include bp-mama-bear {
		h2 {
			font-size: 1em; }
		.cd-read-more, .cd-date {
			font-size: 0.9em; } }

	@include bp-parent-bears {
		margin-left: 0;
		padding: 1.2em;
		width: 45%;

		&::before {
			top: 24px;
			left: 100%;
			border-color: transparent;
			border-left-color: $white; }

		.cd-read-more {
			float: left; }

		.cd-date {
			position: absolute;
			width: 100%;
			left: 122%;
			top: 6px;
			font-size: 0.9em; }

		.cd-timeline-block:nth-child(even) & {
			float: right;

			&::before {
				top: 24px;
				left: auto;
				right: 100%;
				border-color: transparent;
				border-right-color: $white; }

			.cd-read-more {
				float: right; }

			.cd-date {
				left: auto;
				right: 122%;
				text-align: right; }

			.cssanimations &.is-hidden {
				visibility: hidden; }

			.cssanimations &.bounce-in {
				visibility: visible;
				@include animation(cd-bounce-2 .6s); } } } }
@include bp-parent-bears {
	/* inverse bounce effect on even content blocks */
	.cssanimations .cd-timeline-block:nth-child(even) .cd-timeline-content.bounce-in {
		@include animation(cd-bounce-2-inverse .6s); } }
@include keyframes(cd-bounce-2) {
	0% {
		opacity: 0;
		@include transform(translateX(-100px)); }
	60% {
		opacity: 1;
		@include transform(translateX(20px)); }
	100% {
		@include transform(translateX(0)); } }
@include keyframes(cd-bounce-2-inverse) {
	0% {
		opacity: 0;
		@include transform(translateX(100px)); }
	60% {
		opacity: 1;
		@include transform(translateX(-20px)); }
	100% {
		@include transform(translateX(0)); } }

